  methods: {
    async Predict_Axios() { // bu da çalışıyor harici JS dosyası içinde
      try {
        this.params = this.AMT_ANNUITY+"&"+this.AMT_CREDIT+"&"+this.DAYS_EMPLOYED+"&"+this.EXT_SOURCE_1
        +"&"+this.EXT_SOURCE_2+"&"+this.EXT_SOURCE_3+"&"+this.DAYS_BIRTH+"&"+this.DAYS_LAST_PHONE_CHANGE+"&"
        +this.AMT_GOODS_PRICE+"&"+this.DAYS_ID_PUBLISH

        this.result = await predictService.getPrediction(this.params);
        console.log(this.result);
      } catch (error) {
        this.error = error.message;
      }
    }, 

    Predict: function() { // bu da çalışıyor vue dosyası içinde
      this.params = this.AMT_ANNUITY+"&"+this.AMT_CREDIT+"&"+this.DAYS_EMPLOYED+"&"+this.EXT_SOURCE_1
      +"&"+this.EXT_SOURCE_2+"&"+this.EXT_SOURCE_3+"&"+this.DAYS_BIRTH+"&"+this.DAYS_LAST_PHONE_CHANGE+"&"
      +this.AMT_GOODS_PRICE+"&"+this.DAYS_ID_PUBLISH
      
      fetch(`http://home-credit-dsmlbc5.herokuapp.com/predict/${this.params}`)
        .then((resp) => resp.json())
        .then((data) => {
          console.log(data);
          this.result = data;
        });
    },
