const axios = require('axios');

// backend temel adresi, şimdilik localhost ama gerçek backend adresi olmalı ileride...
//const url = 'http://localhost:5000/predict/';
//const url = 'http://home-credit-dsmlbc5.herokuapp.com/predict/';
const url = 'predict/';

class predictService {

    static async getPrediction(params) {
        try {
            // alttaki await axiosun getirmesini beklemek için 
            const resp = await axios.get(url+params);
            console.log(resp.data)
            const data = resp.data;
            console.log(data)
            return data;
            
        } catch (error) {
            console.log(error);
            throw(error);
        } 
    }
    
}
export default predictService;
